<template>
	<view class="container">
		<!-- È°µÈù¢Ê†áÈ¢ò -->
		<view class="page-header">
			<view class="header-title">ËØÑ‰º∞ÂéÜÂè≤ËÆ∞ÂΩï</view>
			<view class="header-subtitle">Êü•ÁúãÊÇ®Â≠©Â≠êÁöÑÂéÜÂè≤ËØÑ‰º∞ËÆ∞ÂΩï</view>
		</view>
		
		<!-- ÂéÜÂè≤ËÆ∞ÂΩïÂàóË°® -->
		<view class="history-list" v-if="historyList.length > 0">
			<view 
				class="history-item" 
				v-for="(record, index) in historyList" 
				:key="index"
				@click="viewRecord(record)"
			>
				<view class="record-header">
					<view class="child-info">
						<text class="child-name">{{ record.childInfo.name }}</text>
						<text class="record-date">{{ formatDate(record.assessmentDate) }}</text>
					</view>
					<view class="score-badge" :class="getScoreClass(record.scorePercentage)">
						{{ Math.round(record.scorePercentage) }}ÂàÜ
					</view>
				</view>
				
				<view class="record-content">
					<view class="assessment-types">
						<text 
							class="type-tag" 
							v-for="type in record.assessmentTypes" 
							:key="type"
						>
							{{ getTypeLabel(type) }}
						</text>
					</view>
					
					<view class="record-summary">
						<text class="summary-text">{{ getRecordSummary(record) }}</text>
					</view>
				</view>
				
				<view class="record-footer">
					<view class="record-age">
						ËØÑ‰º∞Êó∂Âπ¥ÈæÑÔºö{{ calculateAgeAtAssessment(record) }}
					</view>
					<view class="view-btn">
						<text>Êü•ÁúãËØ¶ÊÉÖ ></text>
					</view>
				</view>
			</view>
		</view>
		
		<!-- Á©∫Áä∂ÊÄÅ -->
		<view class="empty-state" v-else>
			<view class="empty-icon">üìä</view>
			<view class="empty-title">ÊöÇÊó†ËØÑ‰º∞ËÆ∞ÂΩï</view>
			<view class="empty-description">
				ÊÇ®ËøòÊ≤°ÊúâËøõË°åËøá‰ªª‰ΩïËØÑ‰º∞<br/>
				ÁÇπÂáª‰∏ãÊñπÊåâÈíÆÂºÄÂßãÁ¨¨‰∏ÄÊ¨°ËØÑ‰º∞
			</view>
			<button class="start-assessment-btn" @click="startNewAssessment">
				ÂºÄÂßãËØÑ‰º∞
			</button>
		</view>
		
		<!-- Â∫ïÈÉ®Êìç‰Ωú -->
		<view class="footer-actions" v-if="historyList.length > 0">
			<button class="btn-secondary" @click="clearHistory">Ê∏ÖÁ©∫ËÆ∞ÂΩï</button>
			<button class="btn-primary" @click="startNewAssessment">Êñ∞ÁöÑËØÑ‰º∞</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				historyList: [],
				typeLabels: {
					motor: 'ËøêÂä®ÂèëËÇ≤',
					language: 'ËØ≠Ë®ÄÂèëËÇ≤',
					cognitive: 'ËÆ§Áü•ÂèëËÇ≤',
					social: 'Á§æ‰∫§ÂèëËÇ≤'
				}
			}
		},
		onLoad() {
			this.loadHistory();
		},
		onShow() {
			// ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂ÈáçÊñ∞Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩï
			this.loadHistory();
		},
		methods: {
			// Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩï
			loadHistory() {
				this.historyList = uni.getStorageSync('assessmentHistory') || [];
			},
			
			// Êü•ÁúãËÆ∞ÂΩïËØ¶ÊÉÖ
			viewRecord(record) {
				// Â∞ÜÈÄâ‰∏≠ÁöÑËÆ∞ÂΩïËÆæÁΩÆ‰∏∫ÂΩìÂâçÁªìÊûú
				uni.setStorageSync('assessmentResult', record);
				
				// Ë∑≥ËΩ¨Âà∞ÁªìÊûúÈ°µÈù¢
				uni.navigateTo({
					url: '/pages/result/result'
				});
			},
			
			// ÂºÄÂßãÊñ∞ËØÑ‰º∞
			startNewAssessment() {
				uni.navigateTo({
					url: '/pages/child-info/child-info'
				});
			},
			
			// Ê∏ÖÁ©∫ÂéÜÂè≤ËÆ∞ÂΩï
			clearHistory() {
				uni.showModal({
					title: 'Ê∏ÖÁ©∫ËÆ∞ÂΩï',
					content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÂéÜÂè≤ËÆ∞ÂΩïÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ',
					success: (res) => {
						if (res.confirm) {
							uni.removeStorageSync('assessmentHistory');
							this.historyList = [];
							uni.showToast({
								title: 'ËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫',
								icon: 'success'
							});
						}
					}
				});
			},
			
			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(dateString) {
				const date = new Date(dateString);
				const now = new Date();
				const diffTime = now - date;
				const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
				
				if (diffDays === 0) {
					return '‰ªäÂ§©';
				} else if (diffDays === 1) {
					return 'Êò®Â§©';
				} else if (diffDays < 7) {
					return `${diffDays}Â§©Ââç`;
				} else {
					return `${date.getFullYear()}Âπ¥${date.getMonth() + 1}Êúà${date.getDate()}Êó•`;
				}
			},
			
			// Ëé∑ÂèñÁ±ªÂûãÊ†áÁ≠æ
			getTypeLabel(type) {
				return this.typeLabels[type] || type;
			},
			
			// Ëé∑ÂèñËØÑÂàÜÊ†∑ÂºèÁ±ª
			getScoreClass(percentage) {
				if (percentage >= 80) return 'excellent';
				if (percentage >= 60) return 'good';
				if (percentage >= 40) return 'average';
				return 'concern';
			},
			
			// Ëé∑ÂèñËÆ∞ÂΩïÊëòË¶Å
			getRecordSummary(record) {
				const percentage = record.scorePercentage;
				if (percentage >= 80) {
					return 'ÂèëËÇ≤Ê∞¥Âπ≥‰ºòÁßÄÔºåÂêÑÈ°πËÉΩÂäõÂèëÂ±ïËâØÂ•Ω';
				} else if (percentage >= 60) {
					return 'ÂèëËÇ≤Ê∞¥Âπ≥ËâØÂ•ΩÔºåÁ¨¶ÂêàÂπ¥ÈæÑÊ†áÂáÜ';
				} else if (percentage >= 40) {
					return 'ÂèëËÇ≤Ê∞¥Âπ≥‰∏ÄËà¨ÔºåÊúâÊèêÂçáÁ©∫Èó¥';
				} else {
					return 'ÂèëËÇ≤Ê∞¥Âπ≥ÈúÄË¶ÅÂÖ≥Ê≥®ÔºåÂª∫ËÆÆ‰∏ì‰∏öÂí®ËØ¢';
				}
			},
			
			// ËÆ°ÁÆóËØÑ‰º∞Êó∂ÁöÑÂπ¥ÈæÑ
			calculateAgeAtAssessment(record) {
				if (!record.childInfo || !record.childInfo.birthDate) return '';
				
				const birth = new Date(record.childInfo.birthDate);
				const assessmentDate = new Date(record.assessmentDate);
				const ageInMonths = (assessmentDate.getFullYear() - birth.getFullYear()) * 12 + 
								   (assessmentDate.getMonth() - birth.getMonth());
				
				if (ageInMonths < 12) {
					return `${ageInMonths}‰∏™Êúà`;
				} else {
					const years = Math.floor(ageInMonths / 12);
					const months = ageInMonths % 12;
					return months > 0 ? `${years}Â≤Å${months}‰∏™Êúà` : `${years}Â≤Å`;
				}
			}
		}
	}
</script>

<style scoped>
