		min-height: 100vh;
		background-color: #F5F9FA;
		padding: 30rpx;
	}
	
	.page-header {
		text-align: center;
		margin-bottom: 40rpx;
	}
	
	.header-title {
		font-size: 36rpx;
		font-weight: bold;
		color: #2C405A;
		margin-bottom: 15rpx;
	}
	
	.header-subtitle {
		font-size: 26rpx;
		color: #666;
		line-height: 1.4;
	}
	
	.history-list {
		margin-bottom: 40rpx;
	}
	
	.history-item {
		background-color: white;
		border-radius: 20rpx;
		padding: 30rpx;
		margin-bottom: 20rpx;
		box-shadow: 0 4rpx 20rpx rgba(168, 216, 234, 0.1);
		transition: transform 0.2s ease;
	}
	
	.history-item:active {
		transform: scale(0.98);
	}
	
	.record-header {
